<template>
	<div class="hello">
		<h1>{{ msg }}</h1>
		<select v-model="locale">
			<option
				v-for="item in localeOptions"
				:key="`locale-${item.lang}`"
				:value="item.lang">
				{{ item.name }}
			</option>
		</select>
		<h3>{{ t("mainTitle.studioName") }}</h3>
        <h3>{{ t("subTitle.name") }}</h3>
	</div>
</template>

<script>
import { useI18n } from 'vue-i18n'

export default {
	name: "HomePage",
	props: {
		msg: String,
	},
	setup() {
		const { t, locale } = useI18n();
		return {
			t,
			locale,
			localeOptions: [
				{
					lang: "en",
					name: "English",
				},
				{
					lang: "zh",
					name: "繁體中文",
				},
			],
		};
	},
};
</script>

